import {Box, Grid} from "@mui/material";
import React from "react";
import Education from "./Education";
import Certificate from "./Certificate";
import {info} from "../../info/Info";

const AboutMe = () => {
    return (
        <Box display={'flex'} flexDirection={'column'} alignItems={'center'} mt={'5rem'} id={"about"}>
            <Grid container md={6} xs={12} alignItems={"flex-start"}>
                <Grid container md={6} xs={12} p={"2rem"}>
                    <h2 style={{marginTop: "2vh"}}>Education</h2>
                    {
                        info.education.map((education) => <Education education={education}/>)
                    }
                </Grid>

                <Grid container md={6} xs={12} p={"2rem"}>
                    <h2 style={{marginTop: "2vh"}}>Certificate</h2>
                    {
                        info.certificate.map((certificate) => <Certificate certificate={certificate} /> )
                    }
                </Grid>
            </Grid>
        </Box>
    )
}

export default AboutMe;