import React, {useState} from 'react';
import {Light as SyntaxHighlighter} from 'react-syntax-highlighter';
import {docco} from 'react-syntax-highlighter/dist/esm/styles/hljs';
import {Button} from "@mui/material";

const SafariQuery = () => {
    const [show, setShow] = useState(false);

    const sqlCode = `
--  

SELECT
      p.project_no,
      p.project_auth,
      p.project_name,
      p.project_keep,
      DATE_FORMAT(p.create_date, '%Y-%m-%d'),
      IF(ISNULL(p.project_end), '', '') AS projectEnd,
      ROUND(
        COALESCE(
          (SELECT COUNT(t.task_end) / COUNT(*) * 100
           FROM task_list tl
           INNER JOIN task t ON t.tasklist_no = tl.tasklist_no
           WHERE tl.project_no = p.project_no), 0)
        , 1) AS complete,
      (SELECT work_member_no
       FROM project_member
       WHERE active = 'Y'
         AND project_member_auth = 'Y'
         AND project_no = p.project_no
         AND work_member_no = #{workMemberNo}) AS manager,
      (SELECT work_member_no
       FROM project_bookmark
       WHERE project_no = p.project_no
         AND work_member_no = #{workMemberNo}) AS projectBookmark
  FROM project p
  
  --   
  <if test="'my'.equals(section)">
      INNER JOIN project_member pm ON pm.project_no = p.project_no
  </if>

  --    
  <if test="projectGroupNo != null">
      INNER JOIN project_group_conn pgc ON pgc.project_no = p.project_no
  </if>

  -- 
  <if test="'bookmark'.equals(section)">
      INNER JOIN project_bookmark pb ON p.project_no = pb.project_no
  </if>

  WHERE work_no = #{workNo}
    AND (p.project_auth = 'N'
         OR p.project_no IN (SELECT p.project_no
                            FROM project p
                                     INNER JOIN project_member pm ON p.project_no = pm.project_no
                            WHERE work_member_no = #{workMemberNo}
                              AND project_auth = 'Y'))

  --   
  <if test="'my'.equals(section)">
      AND pm.work_member_no = #{workMemberNo}
  </if>

  --  
  <if test="'keep'.equals(section)">
      AND p.project_keep = 'Y'
  </if>

  --    
  <if test="projectGroupNo != null">
      AND pgc.projectgroup_no = #{projectGroupNo}
  </if>

  <if test="'bookmark'.equals(section)">
      AND pb.work_member_no = #{workMemberNo}
  </if>

  -- 
  <if test="search != null">
      AND p.project_name LIKE CONCAT('%', #{search}, '%')
  </if>

  <if test="finish != null">
      AND p.project_end IS NULL
  </if>

  -- 
  <if test="''.equals(order) || 'old'.equals(order)">
      ORDER BY p.project_no
  </if>
  <if test="'new'.equals(order)">
      ORDER BY p.create_date DESC
  </if>
  <if test="'abc'.equals(order)">
      ORDER BY p.project_name
  </if>
`;


    return (
        <div>
            <Button variant={"contained"} size={"small"} onClick={() => setShow(!show)}>{show ? "" : " "}</Button>
            {
                !show
                    ? <></>
                    : <SyntaxHighlighter language={"sql"} style={docco}>
                        {sqlCode}
                    </SyntaxHighlighter>
            }
        </div>
    );
};

export default SafariQuery;
