import {Box, Card, Chip, Grid} from "@mui/material";
import Style from "./ProjectCard.module.scss"
import React from "react";
import LinkButton from "./LinkButton";
import ProjectDetail from "./ProjectDetail";

const style = {height: '1.8rem', backgroundColor: "lightgray", fontSize: "0.9rem"}

const ProjectCard = ({project}) => {
    return (
        <Box display={'flex'} flexDirection={{xs: 'column', md: 'row'}} className={Style.wrapper}>
            <Grid md={6} sm={11} container>
                <Card className={Style.card} elevation={3}>
                    <Box className={Style.type}>{project.type}</Box>
                    {project.logo}
                    <Box sx={{mb: "1.5rem"}}>
                        {
                            project.skills.map((skill) =>
                                <Chip label={skill} className={Style.skill} sx={style}/>)
                        }
                    </Box>
                    {/*   -  */}
                    <p className={Style.introduce}>{project.introduce}</p>

                    {/*   -  */}
                    <Box fontSize={"1rem"} mb={"2rem"}>
                        {
                            project.detail.map((detail) => <Box mb={"0.5rem"}>{detail}</Box>)
                        }
                    </Box>

                    <hr style={{marginBottom: "1rem"}}/>

                    {/*  */}
                    <ProjectDetail content={project.content}/>
                    {project.experience}

                    <div className={Style.btns}>
                        <LinkButton value={"github"} link={project.github} icon={"fa fa-github"} />
                        <LinkButton value={"service"} link={project.service} icon={"fa fa-globe"} />
                    </div>
                </Card>
            </Grid>
        </Box>
    )
}

export default ProjectCard