import {Button} from "@mui/material";
import React from "react";

const buttonStyle = {
    marginLeft: "0.5rem",
    marginBottom: "0.5rem",
    width: {xs: "100%", md: "auto"}
};

const LinkButton = ({link, icon, value, className, style, variant = "contained"}) => {
    const props = icon ? {startIcon: <i className={icon} aria-hidden="true"/>} : {}

    const handleClick = () => {
        window.open(link, "_blank");
    };

    return (
        <Button
            className={className}
            variant={variant}
            style={{
                ...buttonStyle, ...style,
            }}
            onClick={handleClick}
            {...props}>
            {value}
        </Button>
    )
}

export default LinkButton