import {info} from "../../info/Info";
import {Box, Card, Grid} from "@mui/material";
import Style from "./Skills.module.scss"

const Skills = () => {
    return (
        <Box id={"skills"} style={{paddingTop: "4rem"}}>
            <h1 className={Style.center}>Skills</h1>
            <Box display="flex" flexDirection={{xs: 'column', md: 'row'}} className={Style.wrapper}>
                <Card elevation={3} className={Style.card}>
                    {info.skills.map((skill) => (
                        <div key={skill.title} className={Style.skillContainer}>
                            <h3>{skill.title}</h3>
                            <ul className={Style.ul}>
                                {skill.details.map((skillDetail, index) => (
                                    <div key={index}>
                                        <li>{skillDetail.item}</li>
                                        <ul className={Style.child}>
                                            {skillDetail.description.map((dep, index) => (
                                                <li key={index}>{dep}</li>
                                            ))}
                                        </ul>
                                    </div>
                                ))}
                            </ul>
                        </div>
                    ))}
                </Card>
            </Box>
        </Box>
    )
}

export default Skills