import {Box, Chip} from "@mui/material";

const style = {
    margin: "2rem 0.4rem 0.7rem",
    height: "1.5rem",
    backgroundColor: "black",
    color: "white"
};

const ulStyle = {
    listStyleType: "disc",
    fontSize: "1rem",
    marginLeft: "2rem"
}

const ProjectDetail = ({content}) => {

    return (
        <Box mb={"1rem"}>
            {
                content?.map((item) => (
                    <>
                        <Chip label={item.title} sx={style}/>
                        <ul style={ulStyle}>
                            {
                                item.description?.map((des) => (des))
                            }
                        </ul>
                    </>
                ))
            }
        </Box>
    )
}

export default ProjectDetail;