import {Chip} from "@mui/material";
import Style from "../chip.module.scss";
import React from "react";

const LinkChip = ({href, label, width = "4.5rem"}) => {
    const style = {height: "1.5rem", width, margin: "0 0.3rem"};

    return (
        <a
            target="_blank" aria-label={"readme"}
            rel="noopener noreferrer"
            href={href}>
            <Chip className={Style.chip} label={label} variant="outlined"
                  sx={style}/>
        </a>
    )
}

export default LinkChip