import React from 'react';
import {Box} from "@mui/material";

function Contact({contact}) {
    const marginRight = {xs: '0.5rem', md: '0.7rem'}

    return (
        <Box fontSize={'1rem'} lineHeight={1.5}>
            <Box component={'span'} aria-label="cheese" role="img" mr={marginRight} fontSize={'1.5rem'}>
                {contact.emoji}
            </Box>
            <Box component={'span'} fontSize={'1rem'} fontWeight={"bold"} mr={marginRight}>
                {contact.label}
            </Box>
            <a target="_blank" aria-label={contact.label} href={contact.link}>
                {contact.text ? contact.text : contact.link}
            </a>
        </Box>
    )
}

export default Contact;